}
.notationWrapper {
	margin: .2em;
	width: 11vw;
	min-width: 4em;
}

label.notationLabel {
	display: inline-block;
	height: 100%;
	width: 100%;
	cursor: pointer;
}

input:checked + label.notationLabel {
	background: red;
	color: whitesmoke;
	border-radius: 25px;
}

.notationContainerWrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
	margin-left: 1em;
	margin-right: 1em;
}
.chartWrapper {
	position: relative;
	display: inline;
}

img {
	width: 70vw;
	border: 2px solid dimgrey;
}

.overlayImage {
	position: absolute;
	bottom: 4px;
	right: 0;
}
